$background: linear-gradient(#69a9bf, #a3d4dd, #f8fcfd);
$search-bar-color: #046f94;
$search-bar-background: rgba(54, 140, 169, 1);
$box-gradient: linear-gradient(#046f94, #3493ad);

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.main {
  background: no-repeat fixed $background;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.container {
  width: 90%;
  max-width: 1000px;
  border-radius: 15px;
  font-size: 30px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  margin: 72px 0 0 0;
}

.wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.splitter {
  display: flex;
  justify-content: space-between;
  width: 95%;
  padding: 5px 20px;
  border-bottom: 1px solid white;
  font-size: 1.6rem;
}

.search-box {
  position: fixed;
  width: 100%;
  max-height: 65px;
  background: $search-bar-color;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-box-container {
  width: 90%;
  max-width: 1000px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.search-box__search-bar {
  width: 90%;
  max-width: 1000px;
  height: 5vh;
  background: $search-bar-background;
  border: 2px solid white;
  border-radius: 15px;
  text-align: left;
  color: white;
  font-size: 1.5rem;
  margin: 10px 0 10px 0;
  padding: 10px;
}

.search-box__search-bar::placeholder {
  text-align: center;
  color: white;
  font-size: 1.5rem;
}

.unit-button {
  height: 40px;
  width: 40px;
  color: white;
  margin-left: 5px;
  font-size: 1.5rem;
  background: $search-bar-background;
  border: 2px solid white;
  border-radius: 15px;
}

.unit-button-active{
  color: black;
  background: white;
}

.location-box {
  background: $box-gradient;
  border-radius: 15px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: white;
  padding: 10px 20px 10px 20px;
  margin-bottom: 10px;
}


.location-box__big {
  background: $box-gradient;
  border-radius: 15px;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  color: white;
  padding: 10px 20px 10px 20px;
  margin-bottom: 10px;
}


.location-box__location {
  font-size: 1.5rem;
}

.location-box__location__big {
  font-size: 4rem;
  text-align: center;
}

.reversed {
  text-align: end;
}


.weather_icon {
  width: 50%;
  max-width: 200px;
}

.weather_icon__big {
  width: 60%;
  max-width: 300px;

}

.info-box {
  background: rgba(0, 0, 0, 0.2);
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
}

.info-box__name {
  text-transform: capitalize;
}

.location-box__city-name {
  text-transform: capitalize;
  font-size: 1.5rem;
}

.future-forecast {
  border-radius: 15px;
  background: $box-gradient;
  padding: 10px 20px 10px 20px;
  margin: 10px 0;
  color: white;
  align-items: center;
}

.future-forecast__header {
  font-size: 3rem;
  margin: 10px 0;
}

.future-forecast__single-day {
  width: 100%;
  font-size: 1.6rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid white;
}

.future-forecast__section {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.future-forecast__2ndsection {
  display: flex;
  align-items: center;
}

.last {
  border: none;
}

.location-box__temp {
  font-size: 4rem;
  font-weight: bold;
  margin: 10px 0;
}

